"use client"

import { useEffect, useRef, useMemo } from "react"
import { motion, useAnimation, useMotionValue, useSpring, useTransform } from "framer-motion"
import { Monitor, Cpu, Globe, Server, Code2, Terminal, Database, Cloud } from "lucide-react"

const symbols = [
    { icon: Code2, x: 20, y: 20, scale: 1.2, delay: 0 },
    { icon: Terminal, x: 80, y: 15, scale: 0.9, delay: 1 },
    { icon: Database, x: 15, y: 80, scale: 1.1, delay: 2 },
    { icon: Cloud, x: 85, y: 75, scale: 1.0, delay: 3 },
    { icon: Monitor, x: 50, y: 10, scale: 0.8, delay: 1.5 },
    { icon: Server, x: 10, y: 50, scale: 0.9, delay: 2.5 },
    { icon: Cpu, x: 90, y: 50, scale: 1.1, delay: 0.5 },
]

const codeSnippets = [
    "const future = await build()",
    "if (ready) launch()",
    "import { success } from 'u'",
    "<Connect />",
    "git push origin master",
]

export default function ProgrammingAnimation() {
    const containerRef = useRef<HTMLDivElement>(null)

    // Mouse interaction
    const mouseX = useMotionValue(0)
    const mouseY = useMotionValue(0)
    const springX = useSpring(mouseX, { damping: 40, stiffness: 400 })
    const springY = useSpring(mouseY, { damping: 40, stiffness: 400 })

    const handleMouseMove = (e: React.MouseEvent) => {
        if (!containerRef.current) return
        const rect = containerRef.current.getBoundingClientRect()
        const x = (e.clientX - rect.left) / rect.width - 0.5
        const y = (e.clientY - rect.top) / rect.height - 0.5
        mouseX.set(x * 20)
        mouseY.set(y * 20)
    }

    const handleMouseLeave = () => {
        mouseX.set(0)
        mouseY.set(0)
    }

    return (
        <div
            ref={containerRef}
            className="relative w-full h-full overflow-hidden bg-zinc-950/50 rounded-xl border border-white/10 backdrop-blur-sm group"
            onMouseMove={handleMouseMove}
            onMouseLeave={handleMouseLeave}
        >
            {/* Background Grid */}
            <div className="absolute inset-0 bg-[linear-gradient(rgba(255,255,255,0.03)_1px,transparent_1px),linear-gradient(90deg,rgba(255,255,255,0.03)_1px,transparent_1px)] bg-[size:40px_40px] opacity-20" />

            {/* Center Core */}
            <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 z-20">
                <motion.div
                    className="relative w-24 h-24 flex items-center justify-center"
                    animate={{ scale: [1, 1.05, 1] }}
                    transition={{ duration: 4, repeat: Number.POSITIVE_INFINITY, ease: "easeInOut" }}
                >
                    {/* Orbital Rings */}
                    <div className="absolute inset-0 border border-cyan-500/30 rounded-full animate-[spin_10s_linear_infinite]" />
                    <div className="absolute inset-2 border border-purple-500/30 rounded-full animate-[spin_15s_linear_infinite_reverse]" />

                    {/* Core Glow */}
                    <div className="absolute inset-0 bg-cyan-500/20 blur-xl rounded-full animate-pulse" />

                    <Globe className="w-10 h-10 text-white relative z-10" strokeWidth={1.5} />
                </motion.div>
            </div>

            {/* Floating Symbols */}
            {symbols.map((item, i) => {
                const Icon = item.icon
                return (
                    <motion.div
                        key={i}
                        className="absolute p-3 rounded-xl bg-white/5 border border-white/10 backdrop-blur-md shadow-lg z-10"
                        style={{
                            left: `${item.x}%`,
                            top: `${item.y}%`,
                            x: useTransform(springX, (val) => val * (Math.random() * 2 + 1)),
                            y: useTransform(springY, (val) => val * (Math.random() * 2 + 1)),
                        }}
                        initial={{ opacity: 0, scale: 0 }}
                        animate={{
                            opacity: 1,
                            scale: item.scale,
                            y: [0, -10, 0],
                        }}
                        transition={{
                            scale: { duration: 0.5, delay: item.delay },
                            opacity: { duration: 0.5, delay: item.delay },
                            y: { duration: 4, repeat: Number.POSITIVE_INFINITY, ease: "easeInOut", delay: Math.random() * 2 }
                        }}
                        whileHover={{ scale: 1.2, borderColor: "rgba(255,255,255,0.5)", backgroundColor: "rgba(255,255,255,0.1)" }}
                    >
                        <Icon className="w-5 h-5 text-cyan-400" strokeWidth={1.5} />
                    </motion.div>
                )
            })}

            {/* Connecting Lines */}
            <svg className="absolute inset-0 w-full h-full pointer-events-none opacity-20">
                {symbols.map((item, i) => (
                    <motion.line
                        key={i}
                        x1="50%"
                        y1="50%"
                        x2={`${item.x}%`}
                        y2={`${item.y}%`}
                        stroke="url(#gradient)"
                        strokeWidth="1"
                        initial={{ pathLength: 0, opacity: 0 }}
                        animate={{ pathLength: 1, opacity: 1 }}
                        transition={{ duration: 1.5, delay: item.delay + 0.5, ease: "easeOut" }}
                    />
                ))}
                <defs>
                    <linearGradient id="gradient" x1="0%" y1="0%" x2="100%" y2="100%">
                        <stop offset="0%" stopColor="#22d3ee" stopOpacity="0" />
                        <stop offset="50%" stopColor="#22d3ee" />
                        <stop offset="100%" stopColor="#22d3ee" stopOpacity="0" />
                    </linearGradient>
                </defs>
            </svg>

            {/* Floating Code Snippets */}
            {codeSnippets.map((code, i) => (
                <motion.div
                    key={i}
                    className="absolute font-mono text-xs text-green-400/40 pointer-events-none whitespace-nowrap"
                    initial={{
                        x: Math.random() * 100 - 50,
                        y: Math.random() * 100 + 100,
                        opacity: 0
                    }}
                    animate={{
                        y: -50,
                        opacity: [0, 0.6, 0]
                    }}
                    transition={{
                        duration: Math.random() * 10 + 15,
                        repeat: Number.POSITIVE_INFINITY,
                        delay: Math.random() * 10,
                        ease: "linear"
                    }}
                    style={{
                        left: `${Math.random() * 80 + 10}%`
                    }}
                >
                    {code}
                </motion.div>
            ))}

            {/* Interactive Cursor Light */}
            <motion.div
                className="absolute w-64 h-64 bg-cyan-500/10 rounded-full blur-[100px] pointer-events-none z-0"
                style={{ x: springX, y: springY, centerX: "50%", centerY: "50%", left: "50%", top: "50%" }}
            />
        </div>
    )
}
